import { ProColumnsType } from '../type'
import { ComponentAdapter } from '../lib/component'

/**
 * ProTable Column 类型
 * 扩展 ProColumns，添加 ProTable 专用字段
 */
export type ProTableColumn = ProColumnsType.ColumnType & {
  /**
   * 在设置中隐藏
   */
  hideInSetting?: boolean
  /**
   * 在表格中隐藏
   */
  hideInTable?: boolean
  /**
   * 默认隐藏（通过 columnsState控制）
   */
  defaultHidden?: boolean
}

/**
 * ProTable 适配器
 * 功能：
 * 1. 转换 columns 为 ProTable 的 columns 格式
 * 2. 处理默认配置：ellipsis、copyable、tooltip 等
 * 3. 适配搜索、隐藏等配置
 */
const ProTableAdapter: ComponentAdapter<ProTableColumn> = {
  name: 'proTable',
  scene: 'table',
  transform: (columns: ProColumnsType.ColumnType[]): ProTableColumn[] => {
    return columns.map((column) => {
      const tableColumn: ProTableColumn = { ...column }

      // 默认启用省略号
      if (!('ellipsis' in tableColumn)) {
        tableColumn.ellipsis = true
      }

      // 搜索配置适配
      if ('search' in tableColumn && tableColumn.search === false) {
        tableColumn.hideInSearch = true
        delete tableColumn.search
      }

      // 保留表格专用配置
      if ('hideInTable' in tableColumn && tableColumn.hideInTable) {
        tableColumn.hideInTable = true
      }

      // 默认隐藏配置（通过 columnsState 控制）
      if ('defaultHidden' in tableColumn && tableColumn.defaultHidden) {
        // ProTable 的 columnsState 默认隐藏配置
        // 具体适配需要根据 ProTable 的实际需求调整
        delete tableColumn.defaultHidden
      }

      return tableColumn
    })
  },
}

export default ProTableAdapter
